.nav_all {
    position: absolute;
    right: 20px;
    bottom: 40px;
    width: 100px;
    height: 184px;
    padding: 2px 0;
    border-radius: 20px;
    background: rgba(255, 255, 255, 0.6);
    backdrop-filter: blur(10px);
}
    .menu {
        width: 100%;
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .menu:nth-child(2) {
        border-style: solid;
        border-width: 1px 0px;
        border-color: #fff;
    }
    a {
        text-decoration: none;
        color: black;
    }