body {
  margin: 0;
  padding: 0;
}
#root {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
.wrap {
  position: relative;
  clear: both;
  width: 340px;
  height: 800px;
  margin: 0 auto;
  background-color: #ddd;
}
  .w_title {
    width: 100%;
    height: 100px;
    position: absolute;
    margin: 0;

    background: rgba(255, 255, 255, 0.4);
    backdrop-filter: blur(10px);
    border-radius:0px 0px 20px 20px;

    display: flex;
    align-items: center;
    justify-content: center;
  }